@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Poppins:ital,wght@0,200;0,300;0,500;0,700;1,800&family=Ubuntu:wght@500&family=Vesper+Libre:wght@700;900&display=swap');

*{
  margin:0px;
  padding:0px;
}

.about-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 50px;
  /* border-radius: 6px; */
}

.about-image {
  width: 300px;
  height: auto;
  margin-right: 50px;
  border-radius: 50px;
}

.about-text {
  max-width: 500px;
}

.about-text h2 {
  margin-top: 0;
  font-size: 100px;
  font-family: 'Dancing Script', cursive;
}

.about-text h3 {
  margin-top: 0;
  font-size: 124px;
  font-weight: normal;
  margin-bottom: 20px;
}

.about-text p {
  font-size: 18px;
  line-height: 1.5;
}
