@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Dosis:wght@300&family=Passions+Conflict&display=swap');

.about-container {
    display: flex;
    flex-direction: row;
    align-items: center;
    padding: 10px;
  }
  
  .left-side {
    flex: 1;
    display:block;
    font-family: 'Dosis', sans-serif;
    font-weight:bolder;

  }
  
  .right-side {
    flex: 0.1;
    display: flex;
    /* justify-content: flex-end; */
    justify-content: center;
  }
  
  .help-button {
    background-color: peachpuff;
    color: white;
    font-size: 20px;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  