@import url('https://fonts.googleapis.com/css2?family=Dancing+Script&family=Dosis:wght@300&family=Passions+Conflict&display=swap');
*{
  margin:0px;
}
.navbar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: #f4b6ac;
  color: white;
  height: 80px;
  padding: 0 40px;
  /* font-size: 42px; */
}

.logo {
  font-size: 34px;
  font-weight: bold;
  width:15%;
}

.links {
  display: flex;
  width:80%;
}

.links a {
  /* margin-left: 20px; */
  margin:20px;
  font-size: 20px;
  font-weight: light;
  text-decoration: none;
  color: inherit;
  font-family: 'Dosis', sans-serif;

}

.links a:hover {
  color: aliceblue;
  text-decoration: none;
  font-weight:bolder;
  text-shadow: 0px 0px 11px white;
  /* background-color: ; */
  transition: 0.4s;
}

