.footer {
    display: flex;
    justify-content: space-around;
    align-items: center;
    background-color: #333;
    color: #fff;
    padding: 30px;
  }
  
  .footer-section {
    flex-basis: 20%;
  }
  
  .footer-section h3 {
    margin-bottom: 10px;
  }
  
  ul {
    list-style: none;
    margin: 0;
    padding: 0;
  }
  
  ul li {
    margin-bottom: 10px;
  }
  
  a {
    color: #fff;
  }
  
  .app-icons {
    display: flex;
    
  }
  
  .app-icons img {
    margin-right: 10px;
  }
  
  .social-icons {
    display: flex;
  }
  
  .social-icons a {
    margin-right: 10px;
    font-size: 1.5rem;
  }
  