.slider {
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-top: 50px;
  }
  
  .slider-cards {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 100%;
    overflow: hidden;
  }
  
  .slider-card {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    text-align: center;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    width: 250px;
    height: 350px;
    margin-right: 20px;
    opacity: 0.5;
    transition: all 0.3s ease;
  }
  
  .slider-card.active {
    opacity: 1;
  }
  
  .slider-card img {
    width: 150px;
    height: 150px;
    border-radius: 50%;
    margin-bottom: 20px;
}
.slider-card h3 {
    margin: 0;
    font-size: 20px;
    font-weight: bold;
    margin-bottom: 10px;
    }
    
    .slider-card p {
    margin: 0;
    font-size: 16px;
    margin-bottom: 20px;
    }
    
    .slider-card button {
    background-color: #7d1578;
    border: none;
    color: #fff;
    padding: 10px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
    }
    
    .slider-card button:hover {
    background-color: #65114f;
    }
    
    .slider-buttons {
    margin-top: 20px;
    }
    
    .slider-buttons button {
    margin-right: 10px;
    background-color: #f9f9f9;
    border: 2px solid #8f3494;
    color: #8e2594;
    padding: 10px 20px;
    border-radius: 5px;
    cursor: pointer;
    transition: all 0.3s ease;
    }
    
    .slider-buttons button:hover {
    background-color: #7f256a;
    color: #fff;
    }
